.navbar {
    background: #30afb3;
    min-height: 70px;
    font-size: 18px;
    padding: 20px 0;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    color: #fff;
    z-index: 99;
    white-space: nowrap;
  }
  
  .navbar div {
    text-align: center;
    margin-bottom: 20px;
    width: 100%;
  }

  .pipe {
    margin: 0 10px;
  }
  
  .correct {
    -webkit-animation: correctGuess 0.5s;
            animation: correctGuess 0.5s;
  }
  .incorrect {
    -webkit-animation: incorrectGuess 0.5s;
            animation: incorrectGuess 0.5s;
  }
  
  @-webkit-keyframes incorrectGuess {
    0% {
      color: white;
    }
    25% {
      color: purple;
    }
    75% {
      color: purple;
    }
    100% {
      color: white;
    }
  }
  
  @keyframes incorrectGuess {
    0% {
      color: white;
    }
    25% {
      color: purple;
    }
    75% {
      color: purple;
    }
    100% {
      color: white;
    }
  }
  
  @-webkit-keyframes correctGuess {
    0% {
      color: white;
    }
    25% {
      color: orange;
    }
    75% {
      color: orange;
    }
    100% {
      color: white;
    }
  }
  
  @keyframes correctGuess {
    0% {
      color: white;
    }
    25% {
      color: orange;
    }
    75% {
      color: orange;
    }
    100% {
      color: white;
    }
  }
  
  @media (min-width: 576px) {
    .navbar {
      display: flex;
      position: fixed;
      justify-content: space-around;
      font-size: calc(10px + 14 * (100vw - 320px) / 800);
    }
  
    .navbar div {
      margin-bottom: 0;
      text-align: center;
      width: 33.333%;
    }
  }
.container {
  margin-top: 40px;
  margin-bottom: 40px;
}

@media (min-width: 1100px) {
  .container {
    padding: 0 100px;
